import React from "react"
import { WOW } from "wowjs"

class Wow extends React.Component {
  componentDidMount() {
    if (typeof window !== "undefined") {
      const wow = new WOW({
        offset: 100,
        mobile: true,
        live: true,
      })

      wow.init()
    }
  }

  componentDidUpdate() {
    this.aos.refresh()
  }

  render() {
    return null
  }
}

export default Wow
